var Vue = require('vue');
var VueRouter = require('vue-router');

// 
Vue.use(VueRouter);

// 
var router = new VueRouter({
    basePath: '/assets/img'
});

router.map({
    '/news': {
        component: function (resolve) {
            require(['./views/news'], resolve);
        }
    },
    '/note': {
        component: function (resolve) {
            require(['./views/note'], resolve);
        }
    },
    '/article': {
        component: function (resolve) {
            require(['./views/article'], resolve);
        }
    }
});

// 
var App = Vue.extend({
    data: function(){
        return {title: ''};
    }
});

//  App  #app 
router.start(App, 'html');


